import Actions from "./action";

const fetchTodos = () => {
  return dispatch => {
    dispatch(Actions.fetchTodoListPending());
    fetch("http://localhost:4000/listToDo")
      .then(res => res.json())
      .then(res => {
        if (res.error) {
          throw res.error;
        }
        dispatch(Actions.fetchTodoListSuccess(res));
        return res;
      })
      .catch(error => {
        dispatch(Actions.fetchTodoListFailed(error));
      });
  };
};

const fetchPriorities = () => {
return dispatch => {
  dispatch(Actions.fetchTodoPrioritiesPending());
  fetch("http://localhost:4000/priority")
    .then(res => res.json())
    .then(res => {
      if (res.error) {
        throw res.error;
      }
      dispatch(Actions.fetchTodoPrioritiesSuccess(res));
      return res;
    })
    .catch(error => {
      dispatch(Actions.fetchTodoPrioritiesFailed(error));
    });
};
};

const todosApiFunctions = {
  fetchTodos,
  fetchPriorities
}

export default todosApiFunctions;

