import React, { Component } from "react";
import { Row, Icon, Button, Col, Empty, Divider } from "antd";
import TaskBox from "./TaskBox";
import Constants from "../../../constants/index";
import { OverPack } from "rc-scroll-anim";
import QueueAnim from "rc-queue-anim";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import todosApiFunctions from "../../../store/actions/todosApiFunctions";
import {
  getTodos,
  getTodosError,
  getTodosPending,
  getTodoPriorities,
  getTodoPrioritiesPending,
  getTodoPrioritiesError
} from "../../../store/reducers/reducer";
const buttonOptions = Constants.TODO_BUTTON_OPTIONS;
class ToDo extends Component {
  handleClick = e => {
    console.log("click ", e);
  };

  componentDidMount() {
    const { fetchTodos, fetchPriorities } = this.props;
    fetchTodos();
    fetchPriorities();
  }

  render() {
    const { todoList, priorityList } = this.props;
    return (
      <div id="todo-container">
        <div id="todo-wrapper">
          <Row type="flex" justify="center">
            <Col span={24}>
              <div className="todo-title">
                <span className="title-span">TO DO LIST</span>
              </div>
            </Col>
            <Col span={24}>
              <Row type="flex" justify="center">
                <span className="content-span date-time">Priorities</span>
              </Row>
            </Col>
            <Col span={24} className="todo-priorities">
              <div className="priorities-wrapper">
                {priorityList.pendingPriorities ? (
                  <div className="priorities">
                    <Icon type="loading" className="loading-data" />
                  </div>
                ) : priorityList.priorities !== undefined ? (
                  priorityList.priorities.map((priority, i) => {
                    return (
                      <div key={i} className="priorities">
                        <div
                          className="color-props priority"
                          style={{
                            backgroundColor: priority.color,
                            boxShadow: `2px 1px 6px -1px ${priority.color}`
                          }}
                          title={priority.name}
                        />
                        <span>{priority.name}</span>
                      </div>
                    );
                  })
                ) : (
                  <div className="priorities">
                    <span>Somethings wrong!</span>
                    <Icon type="frown" />
                  </div>
                )}
              </div>
            </Col>
            <Col span={24}>
              <Row type="flex" justify="center">
                <Col span={4}>
                  <Row type="flex" justify="space-around">
                    {buttonOptions.map((button, i) => {
                      return (
                        <Col
                          span={12}
                          className="todos-list-options-menu"
                          key={i}
                        >
                          <Button
                            className="btn-tasks-option"
                            type={button.buttonType}
                          >
                            <Icon type={button.iconType} />
                            {button.name}
                          </Button>
                        </Col>
                      );
                    })}
                  </Row>
                </Col>
              </Row>
              <Row type="flex" justify="center">
                <Col span={10}>
                  <Divider className="todos-divider" />
                </Col>
              </Row>
            </Col>
            <Col span={22}>
              <Row gutter={16}>
                <OverPack id="todos-overpack">
                  <QueueAnim
                    className="todos-queue"
                    duration={1000}
                    // ease="easeOutBounce"
                    // key="hobbies-queue-title"
                    key="todos-queue"
                    // leaveReverse
                  >
                    {todoList.pendingTodos ? (
                      <Row type="flex" justify="center">
                        <Icon type="loading" className="loading-data" />
                      </Row>
                    ) : todoList.todos !== undefined &&
                      todoList.todos.length > 0 ? (
                      todoList.todos.map((todo, i) => {
                        return (
                          <Col
                            tabIndex={i}
                            key={i}
                            xs={{ span: 24 }}
                            md={{ span: 8 }}
                            lg={{ span: 6 }}
                            xl={{ span: 4 }}
                            className="gutter-row list-col"
                          >
                            <TaskBox data={todo} />
                          </Col>
                        );
                      })
                    ) : (
                      <Empty />
                    )}
                  </QueueAnim>
                </OverPack>
              </Row>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  todoList: {
    todos: getTodos(state),
    pendingTodos: getTodosPending(state),
    error: getTodosError(state)
  },
  priorityList: {
    priorities: getTodoPriorities(state),
    pendingPriorities: getTodoPrioritiesPending(state),
    error: getTodoPrioritiesError(state)
  }
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchTodos: todosApiFunctions.fetchTodos,
      fetchPriorities: todosApiFunctions.fetchPriorities
    },
    dispatch
  );

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ToDo);
